body{
    background-color: #e9ebee;
    color: #1d2129;
    font-size: 14px; 
    font-family:Arial, Helvetica, sans-serif;
    padding-top: 60px;
}
header{
    background-color:#4267B2;
    color: white;
    position:fixed;
    top: 0px;
    z-index: 200;
    left: 0px;
    width: 100%;
}
.toggleHome{  
    display:inline;
}
.candidate .table td .fa{
    padding-right: 0px;
}
.candidate .table td .btn{
    margin: 0px;
}
.user_img{
    width:50px;
    height:50px;
}
.task_bar{
    display: none;
}
.translatey100{
    -webkit-transform: translate(-100%);
        -ms-transform: translate(-100%);
            transform: translate(-100%);
}

.main_nav{
    margin-bottom: 10px;
}
.main_nav .nav>li>a,.blue .nav>li>a{
    background:white;
    color: #1d2129; 
    font-size: 14px;
    font-weight:600;
    margin-bottom: 0px;
    border-right: 1px solid #ccc;
    border-radius:0px;
}
.main_nav .nav>li>a.active,.blue .nav>li.active>a{
    background:#4267B2;
  color: white;
}
.post textarea,.post textarea:focus{
    resize:none;
    font-size:16px;
    width: calc(100% - 60px);
    border: none;
    min-height:85px;
    line-height: 20px;
    padding:8px 10px;
    outline: none;
}
header ul>li>a,header ul>li>a:hover{
    color: white;
    text-decoration: none;
    padding:5px;
    margin: 8px 0px 8px;
    font-size:16px;
    border-radius: 2px;
}
header ul>li>a:hover{
    background-color: #3B5DA0;
    
}
header ul{
    padding: 0px;
    margin: 0px;
    list-style-type: none;
}
header ul>li{
    padding:10px 0px;
    display: inline-block;
}
header .search_input.pull-left{
    width:50%; 
}
header input{
    width:calc(100% - 65px);
    padding:5px;
    margin: 7px 0px;
    border-radius: 2px 0px 0px 2px; 
    border:none;
    margin-left: 15px;
}
header #search{
    color: #1d2129;
    padding:2px 15px 5px;
    margin:7px 0px;
    border: none;
    font-size: 14px;
    border-radius: 0px 2px 2px 0px; 
    background-color:#f6f7f9;
}
.bg-white,.bg-white-hover:hover,.post{
background-color: white;    
border: 1px solid #dddfe2;
border-radius:3px;
    padding:12px;
    display: block;
}
/* app.js */

.active_block a,.active_block a:hover{
    text-decoration:none;
    margin:5px 0px;
    padding: 8px;
    font-weight: 700;
}
.active_block a.active{
    background: #4267B2;
    color: white;
}
.nav_bar 
.nav_bar .nav>li>a{
    padding:6px;
    font-size: 13px;
}
.nav_bar a{
    color: #1d2129;
}
.nav_bar h5{
    font-weight: bold;
}

.nav_bar .nav>li>a:hover{
    background-color: white !important;
    background: #F6F7F9 !important;
border: 1px solid #dddfe2;
border-radius:3px;
    padding: 5px;
}
.nav_bar .fa{
    padding:3px 5px;
}
.nav_bar{
    width:200px;
    float: left;
}

.user_img{
    margin-right: 10px;
}
#logged_user{
    padding: 5px 0px;
}
.user_info h6{
    margin-bottom:0px;
}
.post .fa,.candidate .fa{
    padding-right: 5px;
}
.user_info{
    width: calc(100% - 70px);
}
.post{
    margin:20px 0px;
    padding-bottom: 0px; 
}
.post-header{
    border-radius:2px 2px 0 0;
    font-weight:bold;
    font-size:14px;
}
.post-header a{
    display: inline-block;
}
.post-header .posted_time,.post-footer .posted_time{
    color:#90949c;
    font-size: 12px;
    display: block;
    font-weight: lighter;
}
.create_post{
    margin-top:0px;
    padding-top:0px;
}
.post-header-container{
    margin:0px -12px
}
.create_post .post-header{
    background-color: #F6F7F9;
    padding:5px 12px;
    font-weight: 500;
}
.create_post .post-header span{
    padding:0px 10px;
    border-right: 1px solid #ccc;
}
.create_post .post-footer{
    padding: 5px 0px;
}
.btn-default{
    padding:2px 15px;
    border-radius:1px;
    background-color:#f6f7f9;
    border:1px solid #ced0d4;
    color: #4b4f56;
} 
.btn-default:hover{
    background: #E9EBEE;
}
.btn-primary{
    color: white;
    background:#4267b2 !important;
    border-color:#4267b2;
}
.btn-primary:hover{
    background:#365899;
} 
.create_post .post-body
{padding: 0px;}
.post-header .user_thumb,.create_post .user_thumb{
    width: 40px;
    height:40px;
    float:left;
    margin-right:10px;
}
.post-header .user_action{margin-top: 2px;}
.row{
    margin-right: 0px;
    margin-left: 0px;
}

.post-body{    
    font-size: 14px;
