body {
    margin: 0;
    font-family: sans-serif;
}

.check {
    width: 30px;
}

#col-view .table>tbody>tr.highlight{
    background: #FFFFCC;
}
.table {
    margin-bottom: 0px;
    background: white;
}

.table>tbody>tr>td {
    vertical-align: middle;
}

input[type=checkbox] {
    margin: 0px;
    -webkit-transform: scale(1.2, 1.2);
        -ms-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
}



.col-xs-12 {
    padding-right: 0px;
}

.options {
    padding: 5px 0px 10px;
    margin: 0px;
}

.navbar {
    border-radius: 0px;
    margin-bottom: 0px;
}

.box-body {
    width: 100%;
    height: 100%;
    /* overflow:scroll; */
}

.box-title {
    display: inline-block;
}

.box-header span {
    padding-top: 15px;
}

.navbar-inverse {
    background-color: #2196F3;
    border-color: #2196F3;
}

.navbar-inverse .navbar-nav>li>a {
    color: white;
}
.options .form-control{
    display: inline;
    width: auto;
}


@media(min-width:1200px){
    /* .container {
        width: 1170px;
        padding: 0px; 
    } */
    
}


@media(max-width:1024px){
    .toggleHome{
        display:inline;
    }

    .nav-bar-manag{
        padding-left: 0px;
    }
    
}


@media(max-width:992px){
   .container-fluid,.new_job_section{
    width:100%;
    padding:0px;
   }
    .candidate{
        width: 100%;
        padding: 0px;
    }
    .posts_section{
        width:100%;
    }
    
    .toggleHome{
        display: inline;
    }
    .nav_bar{
        width:230px;
        padding:10px;
        position:fixed;
        left: 0px;
        top:50px;
        z-index:1000;
        border-right:1px solid #ccc;
        height:100%;
        overflow:auto;
        background:#eee;
        -webkit-transition: 0.5s;
        -o-transition: 0.5s;
        transition: 0.5s;
/*        display:none;*/
    }
    
}




@media(max-width:767px){
 
    .container-fluid{
    width: 100%;
   }
   .post_container{
    width:100%;     
    }
    .tabs .nav-justified{
        width: 150%;
    }
    .nav-justified>li {
    display: table-cell;
    width: 1%;
        float: none;
     }
    .task_bar{
        display: inline;
    }
    .tasks{
        display: none;
        width: 100%;
        position: fixed;
        top: 43px;
    }        
    header .search_input.pull-left{
        display: none;
    }

    .wid-xs-padd-top{
     padding-top:10px;
    }
    
    .wid-xs-no-padd-top{
        padding-top: 0px;
    }
    .main_profile{
        padding:0px;
        padding-top:12px;
    }
}

@media(max-width:640px){
   
    .post_container{
       padding:0px;
    }
    .nav_bar{
        width: 100%;
    }
    .tabs .nav-justified {
        width: 200%;
    }
    .main_nav{
        margin: 0px;
    position: fixed;
    bottom: 0px;
    left: 0px;
    z-index: 1000;
    /* border-top: 1px solid #ccc; */
    -webkit-box-shadow: 0px 0px 10px #ccc;
            box-shadow: 0px 0px 10px #ccc;
    }
    .recent-chat-list.dropdown-menu{
    position: fixed;
    width: 100%;
    z-index: 500;
    top:40px;
    left: 0px;
    float: none;
    height:calc(100% - 80px);
    }
    body{
        padding-bottom:30px;
    }
    .chat_box{
        display: none;
    }
}
@media(max-width:480px){
    .tabs .nav-justified{
    width: 235%;
   }
    .tabs .nav>li>a{
        padding: 10px 5px;
    }
    
    
}

@media(max-width:360px){
    
}





